import { z } from "zod";

// ============================================
// SELLER VALIDATIONS
// ============================================

export const createSellerSchema = z.object({
  hourlyRate: z.number().min(1).max(10000), // $1 - $10,000 per hour
  bio: z.string().max(500).optional(),
  tagline: z.string().max(100).optional(),
  timezone: z.string().optional(),
});

export const updateSellerRateSchema = z.object({
  hourlyRate: z.number().min(1).max(10000),
});

export const sellerQuerySchema = z.object({
  experienceId: z.string().optional(),
  companyId: z.string().optional(),
  minRating: z.coerce.number().min(1).max(5).optional(),
  maxRate: z.coerce.number().optional(),
  cursor: z.string().optional(),
  limit: z.coerce.number().min(1).max(50).default(20),
  sort: z
    .enum(["rating", "sessions", "rate_asc", "rate_desc", "newest"])
    .default("rating"),
});

// ============================================
// PURCHASE VALIDATIONS
// ============================================

export const createPurchaseSchema = z.object({
  sellerId: z.string(),
  units: z.number().int().min(1).max(20), // 30-min units, max 10 hours
  experienceId: z.string().optional(),
  companyId: z.string().optional(),
});

// ============================================
// SESSION VALIDATIONS
// ============================================

export const createSessionSchema = z.object({
  sellerId: z.string(),
  units: z.number().int().min(1).max(4), // 30-min to 2 hours per session
  topic: z.string().min(20).max(500),
  proposedTimes: z.array(z.string().datetime()).min(1).max(5),
  timezone: z.string(),
});

export const acceptSessionSchema = z.object({
  scheduledAt: z.string().datetime(),
});

export const endSessionSchema = z.object({
  actualMinutes: z.number().int().min(1),
});

export const cancelSessionSchema = z.object({
  reason: z.string().max(500).optional(),
});

export const sessionQuerySchema = z.object({
  role: z.enum(["buyer", "seller"]).optional(),
  status: z.string().optional(),
  cursor: z.string().optional(),
  limit: z.coerce.number().min(1).max(50).default(20),
});

export const disputeSessionSchema = z.object({
  reason: z.string().min(20).max(1000),
});

export const resolveDisputeSchema = z.object({
  action: z.enum(["approve", "deny"]),
  amountApproved: z.number().int().min(0).optional(),
  reviewNotes: z.string().max(1000).optional(),
});

// ============================================
// REVIEW VALIDATIONS
// ============================================

export const createReviewSchema = z.object({
  rating: z.number().int().min(1).max(5),
  text: z.string().max(1000).optional(),
  isAnonymous: z.boolean().optional(),
});

// ============================================
// COMMUNITY CONFIG VALIDATIONS
// ============================================

export const updateCommunityConfigSchema = z.object({
  communityFeeBps: z.number().int().min(0).max(5000).optional(), // 0-50%
  minSellerRating: z.number().min(1).max(5).optional().nullable(),
  requireApproval: z.boolean().optional(),
});

// ============================================
// REFUND VALIDATIONS
// ============================================

export const createRefundRequestSchema = z.object({
  purchaseId: z.string().optional(),
  sessionId: z.string().optional(),
  reason: z.string().min(10).max(1000),
  amountRequested: z.number().int().min(1),
});

// ============================================
// TYPE EXPORTS
// ============================================

export type CreateSellerInput = z.infer<typeof createSellerSchema>;
export type UpdateSellerRateInput = z.infer<typeof updateSellerRateSchema>;
export type SellerQueryInput = z.infer<typeof sellerQuerySchema>;
export type CreatePurchaseInput = z.infer<typeof createPurchaseSchema>;
export type CreateSessionInput = z.infer<typeof createSessionSchema>;
export type AcceptSessionInput = z.infer<typeof acceptSessionSchema>;
export type EndSessionInput = z.infer<typeof endSessionSchema>;
export type CancelSessionInput = z.infer<typeof cancelSessionSchema>;
export type CreateReviewInput = z.infer<typeof createReviewSchema>;
export type UpdateCommunityConfigInput = z.infer<typeof updateCommunityConfigSchema>;
export type CreateRefundRequestInput = z.infer<typeof createRefundRequestSchema>;
export type SessionQueryInput = z.infer<typeof sessionQuerySchema>;
export type DisputeSessionInput = z.infer<typeof disputeSessionSchema>;
export type ResolveDisputeInput = z.infer<typeof resolveDisputeSchema>;
