import { headers, cookies } from "next/headers";
import { whopsdk } from "./whop-sdk";
import { prisma } from "./prisma";

export interface WhopUser {
  userId: string;
  name: string | null;
  username: string;
  email?: string;
  profilePicUrl?: string;
}

/**
 * Get the current Whop user from headers
 * Returns null if not authenticated
 * In development, can use DEV_WHOP_USER_ID env var to bypass auth
 */
export async function getWhopUser(): Promise<WhopUser | null> {
  try {
    const cookieStore = await cookies();

    // Check if user explicitly logged out - skip ALL Whop auth
    const loggedOut = cookieStore.get("waliet-logged-out")?.value === "true";
    if (loggedOut) {
      return null;
    }

    let userId: string | null = null;

    // In development, allow bypassing auth with DEV_WHOP_USER_ID
    if (process.env.NODE_ENV === "development" && process.env.DEV_WHOP_USER_ID) {
      userId = process.env.DEV_WHOP_USER_ID;
    } else {
      // Normal auth flow - verify token from headers (middleware handles dev token)
      const headersList = await headers();
      const whopToken = headersList.get("x-whop-user-token");

      console.log("[Auth] Token present:", !!whopToken);

      if (!whopToken) {
        console.error("[Auth] No Whop token found in headers");
        return null;
      }

      try {
        const result = await whopsdk.verifyUserToken(headersList);
        userId = result.userId;
        console.log("[Auth] Verified userId:", userId);
      } catch (err) {
        console.error("[Auth] Token verification failed:", err);
        return null;
      }
    }

    if (!userId) return null;

    // Fetch full user data from Whop
    const user = await whopsdk.users.retrieve(userId);

    // Cast to extended type for additional properties not in SDK types
    type ExtendedWhopUser = { email?: string };
    const extendedUser = user as unknown as ExtendedWhopUser;

    return {
      userId,
      name: user.name,
      username: user.username,
      email: extendedUser.email,
      profilePicUrl: user.profile_picture?.url || undefined,
    };
  } catch {
    return null;
  }
}

/**
 * Sync Whop user to database
 * Creates user if doesn't exist, updates if profile changed
 * Uses upsert to handle race conditions
 */
export async function syncUserToDatabase(whopUser: WhopUser) {
  return prisma.user.upsert({
    where: { whopUserId: whopUser.userId },
    update: {
      name: whopUser.name,
      avatar: whopUser.profilePicUrl || null,
      email: whopUser.email || undefined,
    },
    create: {
      whopUserId: whopUser.userId,
      name: whopUser.name,
      avatar: whopUser.profilePicUrl || null,
      email: whopUser.email || null,
    },
    include: { sellerProfile: true },
  });
}

/**
 * Get authenticated user with synced database profile
 * Use this in server components that need auth
 */
export async function getAuthenticatedUser() {
  const whopUser = await getWhopUser();

  if (!whopUser) return null;

  const user = await syncUserToDatabase(whopUser);

  return {
    ...whopUser,
    user,
  };
}

/**
 * Check if user has access to an experience
 * In development with DEV_WHOP_USER_ID, always returns true
 */
export async function checkExperienceAccess(
  experienceId: string,
  userId: string
): Promise<boolean> {
  // In development with dev user, bypass access check
  if (process.env.NODE_ENV === "development" && process.env.DEV_WHOP_USER_ID) {
    return true;
  }

  try {
    const access = await whopsdk.users.checkAccess(experienceId, { id: userId });
    return access.has_access;
  } catch {
    return false;
  }
}

/**
 * Check if user is an admin of a company
 */
export async function checkCompanyAdmin(
  companyId: string,
  userId: string
): Promise<boolean> {
  try {
    const access = await whopsdk.users.checkAccess(companyId, { id: userId });
    return access.access_level === "admin";
  } catch {
    return false;
  }
}

/**
 * Get or create a user from Whop user ID
 * Used in API routes where we have the userId from verifyUserToken
 */
export async function getOrCreateUser(whopUserId: string) {
  const existingUser = await prisma.user.findUnique({
    where: { whopUserId },
    include: { sellerProfile: true },
  });

  if (existingUser) {
    return existingUser;
  }

  // Fetch user details from Whop
  const whopUser = await whopsdk.users.retrieve(whopUserId);
  type ExtendedWhopUser = { email?: string };
  const extendedUser = whopUser as unknown as ExtendedWhopUser;

  const newUser = await prisma.user.create({
    data: {
      whopUserId,
      email: extendedUser.email ?? null,
      name: whopUser.name,
      avatar: whopUser.profile_picture?.url ?? null,
    },
    include: { sellerProfile: true },
  });

  return newUser;
}
